module.exports = "/* Results CSS */ \r\n\r\n.results-container {\r\n    display: flex;\r\n    flex-direction: row;\r\n    flex-wrap: wrap;\r\n\r\n    border: 1px solid #0000003b;\r\n    border-top: none!important;\r\n    border-bottom-left-radius: 5px;\r\n    border-bottom-right-radius: 5px;\r\n} \r\n\r\n.album-cd-header {\r\n    width: 100%;\r\n    padding: 5px;\r\n    background-color:  rgba(0,0,0,0.5);\r\n    font-weight: bold;\r\n    border-top-left-radius: 5px;\r\n    border-top-right-radius: 5px;\r\n} \r\n\r\n.album-cd-header {\r\n    margin-top: 0.8rem;\r\n} \r\n\r\n.album-cd-header:first-child {\r\n    margin-top: 0rem;\r\n} \r\n\r\n.albums-listed-songs {\r\n    width: 100%;\r\n    display: flex;\r\n    align-items: center;\r\n    box-sizing: border-box;\r\n    flex-direction: row;\r\n    padding: 5px;\r\n    justify-content: space-between;\r\n} \r\n\r\n.save-fav-song {\r\n    cursor: pointer;\r\n    margin: 0px 5px;\r\n    width: 23px;\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n    fill: rgba(0, 0, 0, 0.2);\r\n    position: relative;\r\n} \r\n\r\n.star-icon {\r\n    height: 24px;\r\n    width: 24px;\r\n} \r\n\r\n.save-fav-song:hover {\r\n    transition: all 0.2s;\r\n    fill: rgba(0, 0, 0, 0.6);\r\n} \r\n\r\n.save-fav-song .faved {\r\n    fill: #ffe20094;\r\n    -webkit-animation-name: starring;\r\n            animation-name: starring;\r\n    -webkit-animation-iteration-count: 1;\r\n            animation-iteration-count: 1;\r\n    -webkit-animation-duration: 0.2s;\r\n            animation-duration: 0.2s;\r\n    -webkit-animation-timing-function: ease-in;\r\n            animation-timing-function: ease-in;\r\n\r\n} \r\n\r\n@-webkit-keyframes starring {\r\n    0% { \r\n        -webkit-transform: scale( 1 ); \r\n                transform: scale( 1 );\r\n    }\r\n    50% { \r\n        -webkit-transform: scale( 1.5 ); \r\n                transform: scale( 1.5 );\r\n    }\r\n    100%{\r\n        -webkit-transform: scale( 1 );\r\n                transform: scale( 1 );    \r\n    }\r\n  } \r\n\r\n@keyframes starring {\r\n    0% { \r\n        -webkit-transform: scale( 1 ); \r\n                transform: scale( 1 );\r\n    }\r\n    50% { \r\n        -webkit-transform: scale( 1.5 ); \r\n                transform: scale( 1.5 );\r\n    }\r\n    100%{\r\n        -webkit-transform: scale( 1 );\r\n                transform: scale( 1 );    \r\n    }\r\n  } \r\n\r\n.albums-listed-songs:nth-child(even) {\r\n    background-color: rgba(0,0,0,0.1);\r\n} \r\n\r\n.albums-listed-songs:hover {\r\n    transition: all 0.2s;\r\n    background-color: rgba(0,0,0,0.3);\r\n} \r\n\r\n.albums-listed-songs .image-artists {\r\n    flex-basis: 60px;\r\n} \r\n\r\n.albums-listed-songs .image-artists img{\r\n    border-radius: 100%;\r\n    border: 1px solid rgba(0,0,0,0.2);\r\n} \r\n\r\n.albums-listed-songs .label-artists {\r\n    flex-basis: calc(100% - 70px);\r\n    height: 100%;\r\n    justify-content: center;\r\n    display: flex;\r\n    flex-direction: column;\r\n    margin: 10px;\r\n} \r\n\r\n.albums-listed-songs .label-artists span {\r\n    overflow: hidden;\r\n    text-overflow: ellipsis;\r\n} \r\n\r\n/* Icon Faved Labels*/ \r\n\r\n/* Tooltip text */ \r\n\r\n.save-fav-song .fav-label {\r\n    visibility: hidden;\r\n    width: auto;\r\n    background-color: #28282887;\r\n    color: #fff;\r\n    text-align: center;\r\n    padding: 5px;\r\n    border-radius: 6px;\r\n    left: 1.5rem;\r\n    position: absolute;\r\n    z-index: 1;\r\n    opacity: 0;\r\n    transition: opacity 0.5s;\r\n} \r\n\r\n.save-fav-song:hover .fav-label {\r\n    visibility: visible;\r\n    opacity: 1;\r\n} \r\n\r\n.fav-label::after {\r\n    content: \" \";\r\n    position: absolute;\r\n    top: 50%;\r\n    right: 100%; /* To the left of the tooltip */\r\n    margin-top: -5px;\r\n    border-width: 5px;\r\n    border-style: solid;\r\n    border-color: transparent #28282887 transparent transparent;\r\n}\r\n\r\n/*# sourceMappingURL=data:application/json;base64, */"